#homepage__wrapper {
    -ms-overflow-style: none;
    scrollbar-width: none;
    overflow: visible;

    &::-webkit-scrollbar {
        display: none;
    }

    main {
        position: relative;
        display: block;
        height: calc(100vh - 110px);

        #homepage__projects {
            width: 100%;
            height: 100%;

            .homepage__projects__project {
                position: absolute;
                width: 200px;
                height: 200px;
                background-image: url('https://via.placeholder.com/150');
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
                clip-path: polygon(5% 5%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 50% 75%, 0% 75%);
                overflow: hidden;
                cursor: pointer;

                div {
                    width: 100%;
                    height: 100%;
                    background-color: $red;
                    opacity: 1;
                    transition: all 0.6s cubic-bezier(0.16, 1, 0.3, 1);

                    &:hover {
                        opacity: 0;
                        transition: all 0.6s cubic-bezier(0.16, 1, 0.3, 1);
                    }
                }
            }
        }

        #main__background {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
        }
    }
}